<template>
  <div id="tab-bar">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "tabbar"
};
</script>

<style>
#tab-bar {
  display: flex;
  background-color: #f6f6f6;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  /* 加   $   的都是必须 */
  /* box-shadow:x偏移量（$） y偏移量($) 模糊度  阴影大小 颜色 是否内阴影; */
  box-shadow: 0 -2px 1px rgba(100, 100, 100, 0.1);
}

</style>



